.hero {
  width: 100%;
  height: 100vh;
  color: #ffffff;
  position: relative;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

#video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.hero .content {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
  margin: auto;
  padding: 1rem;
}

.form {
  display: flex;
  background-color: #ffffffcc;
  color: #000;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ccc;
  padding: 4px;
  border-radius: 8px;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
}

.form input[type="text"] {
  background-color: transparent;
  border: transparent;
  max-width: 400px;
  width: 100%;
  font-size: 1.2rem;
  font-family: "Poppins", sans-serif;
  outline: none;
}

